// components/navbar/MenuList.tsx
"use client";

import * as React from "react";
import { cn } from "@/lib/utils";
import {
    IceCream,
    Donut,
    Popcorn,
    CupSoda,
    Cookie,
    Flame,
    Candy,
    ShieldAlert,
    ShoppingCart,
    Percent,
} from "lucide-react";

import {
    NavigationMenu,
    NavigationMenuContent,
    NavigationMenuItem,
    NavigationMenuLink,
    NavigationMenuList,
    NavigationMenuTrigger,
    navigationMenuTriggerStyle,
} from "@/components/ui/navigation-menu";

/* -------------------------------------------------------------------------- */
/*                                   Tipos                                    */
/* -------------------------------------------------------------------------- */

/** Representa un producto destacado mostrado dentro del sub-menú Tienda. */
interface ProductItem {
    id: string;
    title: string;
    href: string;
    description: string;
    icon: React.ElementType;
    price: number;
}

/* -------------------------------------------------------------------------- */
/*                            Componente principal                            */
/* -------------------------------------------------------------------------- */
/**
 * **MenuList**
 *
 * Barra de navegación desplegable:
 * - _Trigger_ “Kwik-E-Mart” con 3 enlaces estáticos.
 * - Sub-menú “Tienda” que lista productos destacados.
 * - Link directo a “Reseñas”.
 */
export default function MenuList() {
    return (
        <NavigationMenu>
            <NavigationMenuList>
                {/* ───────────────── Menú principal ───────────────── */}
                <NavigationMenuItem>
                    <NavigationMenuTrigger>Kwik-E-Mart</NavigationMenuTrigger>
                    <NavigationMenuContent>
                        <ul className="grid gap-3 p-4 md:w-[400px] lg:w-[500px] lg:grid-cols-[.75fr_1fr]">
                            {/* Hero panel */}
                            <li className="row-span-3">
                                <NavigationMenuLink asChild>
                                    <div className="flex h-full w-full select-none flex-col justify-end rounded-md bg-gradient-to-b from-amber-50 to-amber-100 p-6 dark:from-amber-400 dark:to-yellow-600 outline-none focus:shadow-md">
                                        <h1 className="mb-2 text-xl font-bold text-black dark:text-white">
                                            Bienvenido al Kwik-E-Mart
                                        </h1>
                                        <p className="mb-2 text-md font-medium italic">
                                            “Muchas gracias, vuelva pronto”
                                        </p>
                                        <p className="text-sm leading-tight text-muted-foreground dark:text-gray-50">
                                            La tienda de conveniencia más famosa
                                            de Springfield, administrada por el
                                            siempre amable Apu
                                            Nahasapeemapetilon. ¡Ven por tu
                                            Squishee y las donas favoritas de
                                            Homero!
                                        </p>
                                    </div>
                                </NavigationMenuLink>
                            </li>

                            {/* Enlaces estáticos */}
                            <StaticItem
                                href="/productos"
                                title="Tienda"
                                icon={ShoppingCart}>
                                Explora todos los productos, conoce precios y
                                reseñas.
                            </StaticItem>
                            <StaticItem
                                href="/promociones"
                                title="Promociones"
                                icon={Percent}>
                                Ofertas como 2×1 en Squishees o Duff 6×3.
                            </StaticItem>
                            <StaticItem
                                href="/seguridad"
                                title="Seguridad"
                                icon={ShieldAlert}>
                                Medidas para prevenir estafas de Bart.
                            </StaticItem>
                        </ul>
                    </NavigationMenuContent>
                </NavigationMenuItem>

                {/* ───────────────── Sub-menú Tienda ───────────────── */}
                <NavigationMenuItem>
                    <NavigationMenuTrigger>Tienda</NavigationMenuTrigger>
                    <NavigationMenuContent>
                        <ul className="grid w-[400px] gap-3 p-4 md:w-[500px] md:grid-cols-2 lg:w-[600px]">
                            {products.map((product) => (
                                <ProductListItem
                                    key={product.id}
                                    product={product}
                                />
                            ))}
                        </ul>
                    </NavigationMenuContent>
                </NavigationMenuItem>

                {/* ───────────────── Reseñas ───────────────── */}
                <NavigationMenuItem>
                    <NavigationMenuLink
                        href="/resenas"
                        className={navigationMenuTriggerStyle()}>
                        Reseñas
                    </NavigationMenuLink>
                </NavigationMenuItem>
            </NavigationMenuList>
        </NavigationMenu>
    );
}

/* -------------------------------------------------------------------------- */
/*                              Item “estático”                               */
/* -------------------------------------------------------------------------- */
interface StaticItemProps extends React.ComponentPropsWithoutRef<"a"> {
    title: string;
    icon?: React.ElementType;
    children: React.ReactNode;
}

/** Enlace de primer nivel dentro del panel principal. */
const StaticItem = ({
    className,
    title,
    icon: Icon,
    children,
    ...props
}: StaticItemProps) => (
    <li>
        <NavigationMenuLink asChild>
            <a
                className={cn(
                    "group block select-none space-y-1 rounded-md p-3 leading-none outline-none transition-colors",
                    "hover:bg-amber-50 hover:text-black focus:bg-amber-100 focus:text-black dark:hover:bg-yellow-500",
                    className
                )}
                {...props}>
                <div className="flex items-center gap-2">
                    {Icon && (
                        <Icon className="w-5 h-5 dark:stroke-amber-100 " />
                    )}
                    <span className="text-sm font-medium leading-none">
                        {title}
                    </span>
                </div>
                <p className="line-clamp-2 text-sm leading-snug text-muted-foreground dark:group-hover:text-gray-800">
                    {children}
                </p>
            </a>
        </NavigationMenuLink>
    </li>
);

/* -------------------------------------------------------------------------- */
/*                             Item de producto                               */
/* -------------------------------------------------------------------------- */
/** Ítem clicable (solo enlaza – no añade al carrito). */
const ProductListItem = ({ product }: { product: ProductItem }) => (
    <li>
        <button
            className={cn(
                "w-full text-left group block select-none space-y-1 rounded-md p-3 leading-none outline-none transition-colors",
                "hover:bg-amber-50 hover:text-black focus:bg-amber-100 focus:text-black dark:hover:bg-yellow-500"
            )}
            onClick={() => (window.location.href = product.href)}>
            <div className="flex items-center gap-2">
                <product.icon className="w-5 h-5" />
                <span className="text-sm font-medium leading-none">
                    {product.title}
                </span>
            </div>
            <p className="line-clamp-2 text-sm leading-snug text-muted-foreground dark:group-hover:text-gray-700">
                {product.description}
            </p>
        </button>
    </li>
);

/* -------------------------------------------------------------------------- */
/*                           Productos destacados                             */
/* -------------------------------------------------------------------------- */

const products: ProductItem[] = [
    {
        id: "squishee",
        title: "Squishee Clásico",
        href: "/productos/squishee",
        description:
            "La bebida helada favorita de Bart: congelada, colorida y llena de sabor.",
        icon: IceCream,
        price: 2.5,
    },
    {
        id: "donas",
        title: "Donas con chispas",
        href: "/productos/dona-rosada-de-homero",
        description:
            "Las preferidas de Homero. ¡Rosadas, con chispas y rellenas de sorpresas!",
        icon: Donut,
        price: 3.0,
    },
    {
        id: "palomitas",
        title: "Palomitas Radioactivas",
        href: "/productos/palomitas",
        description: "Para acompañar las aventuras de Rasca y Pica.",
        icon: Popcorn,
        price: 2.0,
    },
    {
        id: "buzz-cola",
        title: "Buzz Cola",
        href: "/productos/buzz-cola",
        description: "La bebida energizante que recomienda Krusty.",
        icon: CupSoda,
        price: 1.5,
    },
    {
        id: "galletas-suerte",
        title: "Galletas de la suerte",
        href: "/productos/galletas-suerte",
        description: "Mensajes sabios con un toque de locura, como Apu.",
        icon: Cookie,
        price: 2.2,
    },
    {
        id: "chicles-fuego",
        title: "Chicles de fuego",
        href: "/productos/chicles-fuego",
        description: "¡Un sabor ardiente que hará que tu boca arda!",
        icon: Flame,
        price: 1.0,
    },
    {
        id: "golosinas-flanders",
        title: "Golosinas de Flanders",
        href: "/productos/golosinas-flanders",
        description: "Dulces bendecidos con la ‘sapiencia’ de Ned.",
        icon: Candy,
        price: 2.8,
    },
];
