"use client";

import React, {
    createContext,
    useState,
    useContext,
    useEffect,
    ReactNode,
} from "react";

import { Producto } from "@/lib/data/productos";

// Tipado del contexto de la lista de deseos
interface WishlistContextType {
    wishlistItems: Producto[];
    addToWishlist: (product: Producto) => void;
    removeFromWishlist: (productId: number) => void;
    isInWishlist: (productId: number) => boolean;
}

// Creamos el contexto
const WishlistContext = createContext<WishlistContextType | undefined>(
    undefined
);

// Creamos el Proveedor del contexto. Debería envolver toda la app o las partes que lo necesiten.
export const WishlistProvider = ({ children }: { children: ReactNode }) => {
    const [wishlistItems, setWishlistItems] = useState<Producto[]>([]);

    // Efecto para cargar la lista desde localStorage cuando el componente se monta.
    useEffect(() => {
        try {
            const items = localStorage.getItem("kwik-e-wishlist");
            if (items) {
                setWishlistItems(JSON.parse(items));
            }
        } catch (error) {
            console.error(
                "¡Ay, caramba! No se pudo leer la lista de deseos.",
                error
            );
            setWishlistItems([]);
        }
    }, []);

    // Efecto para guardar la lista en localStorage cada vez que cambie.
    useEffect(() => {
        try {
            localStorage.setItem(
                "kwik-e-wishlist",
                JSON.stringify(wishlistItems)
            );
        } catch (error) {
            console.error(
                "¡D'oh! No se pudo guardar la lista de deseos.",
                error
            );
        }
    }, [wishlistItems]);

    const addToWishlist = (product: Producto) => {
        // Evitamos duplicados, como cuando Homero intenta comprar dos veces la misma rosquilla.
        if (!wishlistItems.some((item) => item.id === product.id)) {
            setWishlistItems((prevItems) => [...prevItems, product]);
        }
    };

    const removeFromWishlist = (productId: number) => {
        setWishlistItems((prevItems) =>
            prevItems.filter((item) => item.id !== productId)
        );
    };

    const isInWishlist = (productId: number) => {
        return wishlistItems.some((item) => item.id === productId);
    };

    return (
        <WishlistContext.Provider
            value={{
                wishlistItems,
                addToWishlist,
                removeFromWishlist,
                isInWishlist,
            }}>
            {children}
        </WishlistContext.Provider>
    );
};

// Hook personalizado para usar el contexto de forma más limpia y segura.
export const useWishlist = () => {
    const context = useContext(WishlistContext);
    if (context === undefined) {
        throw new Error(
            "useWishlist debe ser usado dentro de un WishlistProvider"
        );
    }
    return context;
};
