/**********************************************************************
 * components/CartSidebar.tsx
 * --------------------------------------------------------------------
 * Panel lateral que muestra el carrito y permite ajustar cantidades.
 *  – Se abre/cierra mediante el contexto `CartUIProvider`
 *  – Calcula el subtotal aplicando posibles promociones
 *  – Se oculta por completo en la ruta `/pago`
 *********************************************************************/
"use client";

import { useEffect, useState } from "react";
import { useRouter, usePathname } from "next/navigation";
import { ShoppingCart, Trash2 } from "lucide-react";

import { useCartUI } from "@/lib/cart-ui-context";
import { useCart } from "@/lib/cart-context";
import { getTotalPromo } from "@/lib/precio-producto";

const NAVBAR_ID = "main-navbar"; // id del <header> principal

export default function CartSidebar() {
    /* ─────────────────────── hooks / estados ─────────────────────── */
    const { isOpen, closeCart } = useCartUI();
    const { items, removeItem, updateItemQuantity, clear } = useCart();
    const pathname = usePathname();
    const router = useRouter();

    /* altura dinámica del navbar (para colocar el panel justo debajo) */
    const [navH, setNavH] = useState(0);
    useEffect(() => {
        const calc = () =>
            setNavH(document.getElementById(NAVBAR_ID)?.offsetHeight ?? 0);
        calc();
        window.addEventListener("resize", calc);
        return () => window.removeEventListener("resize", calc);
    }, []);

    /* /pago: se cierra/oculta automáticamente */
    useEffect(() => {
        if (pathname === "/pago") closeCart();
    }, [pathname, closeCart]);
    if (pathname === "/pago") return null;

    /* subtotal con precio promocional ya aplicado por ítem */
    const subtotal = items.reduce(
        (sum, it) => sum + getTotalPromo(it.promo, it.price, it.quantity).total,
        0
    );

    /* ––––––––––––––––––––– helpers UI –––––––––––––––––––– */
    const dec = (id: string) => {
        const it = items.find((i) => i.id === id);
        if (!it) return;
        if (it.quantity > 1) {
            updateItemQuantity(id, it.quantity - 1);
        } else {
            removeItem(id);
        }
    };

    /* ───────────────────────── render ─────────────────────────────── */
    return (
        <>
            {/* Overlay oscuro + blur ------------------------------------- */}
            <div
                onClick={closeCart}
                className={`fixed inset-0 z-40 transition-opacity duration-300
          ${isOpen ? "opacity-100" : "opacity-0 pointer-events-none"}`}
                style={{
                    top: navH,
                    background: "rgba(0,0,0,.35)",
                    backdropFilter: "blur(1.5px)",
                }}
            />

            {/* Panel ------------------------------------------------------ */}
            <aside
                onClick={(e) => e.stopPropagation()}
                className={`
          fixed right-0 z-50 w-80 bg-background text-foreground
          transition-transform duration-300
          ${isOpen ? "translate-x-0" : "translate-x-full"}
        `}
                style={{ top: navH, bottom: 0 }}>
                {/* ── Header ─────────────────────────────────────────────── */}
                <header
                    className="sticky top-0 z-10 flex items-center justify-between
                           border-b border-border bg-background px-4 py-3">
                    <h2 className="text-2xl font-bold">Tu canasta</h2>
                    <button
                        onClick={closeCart}
                        aria-label="Cerrar"
                        className="text-xl font-bold">
                        ×
                    </button>
                </header>

                {/* ── Contenido scrollable ───────────────────────────────── */}
                <div className="h-full overflow-y-auto p-4 space-y-4">
                    {/* Carrito vacío */}
                    {items.length === 0 && <EmptyState closeCart={closeCart} />}

                    {/* Listado de productos */}
                    {items.map((it, idx) => (
                        <CartItem
                            key={`${it.id}-${idx}`}
                            it={it}
                            inc={() =>
                                updateItemQuantity(it.id, it.quantity + 1)
                            }
                            dec={() => dec(it.id)}
                            rm={() => removeItem(it.id)}
                        />
                    ))}

                    {/* Acciones (pagar / vaciar) */}
                    {items.length > 0 && (
                        <div className="space-y-4">
                            <button
                                onClick={() => router.push("/pago")}
                                className="flex w-full items-center justify-between rounded-xl
                           bg-green-500 px-4 py-3 font-semibold text-white
                           hover:bg-green-600">
                                <span>Ir a pagar</span>
                                <span>${subtotal.toFixed(2)}</span>
                            </button>

                            <button
                                onClick={clear}
                                className="flex w-full items-center justify-center gap-2 text-sm
                           text-muted-foreground hover:text-red-500">
                                <Trash2 className="h-4 w-4" /> Vaciar canasta
                            </button>
                        </div>
                    )}
                </div>
            </aside>
        </>
    );
}

/* ===================== sub-componentes auxiliares ====================== */

function EmptyState({ closeCart }: { closeCart: () => void }) {
    const router = useRouter();
    return (
        <div className="flex flex-col items-center gap-4 py-10 text-center">
            <div className="flex h-16 w-16 items-center justify-center rounded-full bg-muted">
                <ShoppingCart className="h-6 w-6 text-muted-foreground" />
            </div>
            <p className="text-sm text-muted-foreground">
                Aún no tienes productos en tu canasta
            </p>
            <button
                onClick={() => {
                    closeCart();
                    router.push("/productos");
                }}
                className="rounded-lg bg-green-500 px-4 py-2 text-sm font-semibold
                   text-black hover:bg-green-600">
                Comenzar a comprar
            </button>
        </div>
    );
}

function CartItem({
    it,
    inc,
    dec,
    rm,
}: {
    it: ReturnType<typeof useCart>["items"][number];
    inc: () => void;
    dec: () => void;
    rm: () => void;
}) {
    const { total, old } = getTotalPromo(it.promo, it.price, it.quantity);

    return (
        <div className="flex gap-3 rounded-lg bg-[#FFF3B0] p-3 dark:bg-yellow-400">
            {/* Imagen */}
            {it.image ? (
                <img
                    src={it.image}
                    alt={it.name}
                    className="h-20 w-20 rounded-full bg-white p-1 object-contain"
                />
            ) : (
                <div className="h-12 w-12 rounded-full bg-white" />
            )}

            {/* Texto y controles */}
            <div className="flex flex-1 flex-col">
                <p className="mb-1 text-sm font-medium text-black">{it.name}</p>

                {/* Opciones (sabor, tamaño, …) */}
                {it.options && (
                    <ul className="mb-1 text-xs text-gray-700 dark:text-gray-900">
                        {Object.entries(it.options).map(([k, v]) => (
                            <li key={k}>
                                <span className="capitalize">{k}</span>: {v}
                            </li>
                        ))}
                    </ul>
                )}

                {/* Precio subtotal */}
                <div className="mb-1 flex items-center gap-2">
                    {old && (
                        <span className="text-base font-semibold line-through text-red-500">
                            ${old.toFixed(2)}
                        </span>
                    )}
                    <span className="text-lg font-extrabold text-green-600">
                        ${total.toFixed(2)}
                    </span>
                </div>

                {/* Controles cantidad */}
                <div className="mt-2 flex items-center gap-1 text-sm text-muted-foreground">
                    {it.quantity === 1 ? (
                        <button
                            onClick={rm}
                            className="rounded p-1 hover:bg-white/30">
                            <Trash2 className="h-4 w-4" />
                        </button>
                    ) : (
                        <button
                            onClick={dec}
                            className="rounded px-2 hover:bg-white/30">
                            –
                        </button>
                    )}
                    <span>{it.quantity} Unidades</span>
                    <button
                        onClick={inc}
                        className="rounded px-2 hover:bg-white/30">
                        +
                    </button>
                </div>
            </div>
        </div>
    );
}
