/**
 * AuthContext.tsx
 *
 * Contexto de autenticación de la aplicación:
 *  - Guarda / restaura usuario y JWTs en localStorage
 *  - Expone login / logout y bandera isAuthenticated
 */

"use client";

import {
    createContext,
    useContext,
    useEffect,
    useState,
    ReactNode,
} from "react";

import type { User, Tokens } from "@/lib/types";

/* -------------------------------------------------------------------------- */
/* Tipado del contexto                                                         */
/* -------------------------------------------------------------------------- */

interface AuthContextType {
    user: User | null;
    tokens: Tokens | null;
    login: (user: User, tokens: Tokens) => void;
    logout: () => void;
    isAuthenticated: boolean;
}

/* -------------------------------------------------------------------------- */
/* Constantes para las claves de storage                                       */
/* -------------------------------------------------------------------------- */

const LS_USER_KEY = "km_user";
const LS_ACCESS_TOKEN = "accessToken";
const LS_REFRESH_TOKEN = "refreshToken";

/* -------------------------------------------------------------------------- */
/* Creación del contexto                                                       */
/* -------------------------------------------------------------------------- */

const AuthContext = createContext<AuthContextType | undefined>(undefined);

/* -------------------------------------------------------------------------- */
/* Provider                                                                    */
/* -------------------------------------------------------------------------- */

export const AuthProvider = ({ children }: { children: ReactNode }) => {
    const [user, setUser] = useState<User | null>(null);
    const [tokens, setTokens] = useState<Tokens | null>(null);

    /* Restaura sesión al montar */
    useEffect(() => {
        const storedUser = localStorage.getItem(LS_USER_KEY);
        const access = localStorage.getItem(LS_ACCESS_TOKEN);
        const refresh = localStorage.getItem(LS_REFRESH_TOKEN);

        if (storedUser && access && refresh) {
            setUser(JSON.parse(storedUser));
            setTokens({ accessToken: access, refreshToken: refresh });
        }
    }, []);

    /* ------------------------- Métodos ------------------------------------ */

    const login = (u: User, t: Tokens) => {
        setUser(u);
        setTokens(t);

        localStorage.setItem(LS_USER_KEY, JSON.stringify(u));
        localStorage.setItem(LS_ACCESS_TOKEN, t.accessToken);
        localStorage.setItem(LS_REFRESH_TOKEN, t.refreshToken);
    };

    const logout = () => {
        setUser(null);
        setTokens(null);

        localStorage.removeItem(LS_USER_KEY);
        localStorage.removeItem(LS_ACCESS_TOKEN);
        localStorage.removeItem(LS_REFRESH_TOKEN);
    };

    return (
        <AuthContext.Provider
            value={{
                user,
                tokens,
                login,
                logout,
                isAuthenticated: !!user && !!tokens,
            }}>
            {children}
        </AuthContext.Provider>
    );
};

/* -------------------------------------------------------------------------- */
/* Hook de conveniencia                                                        */
/* -------------------------------------------------------------------------- */

export const useAuth = (): AuthContextType => {
    const ctx = useContext(AuthContext);
    if (!ctx)
        throw new Error("useAuth debe usarse dentro de <AuthProvider>");
    return ctx;
};
